import { MouseEventHandler, ReactNode } from "react";

export enum Variant {
  PRIMARY = "PRIMARY",
  SECONDARY = "SECONDARY",
  WHITE_PRIMARY = "WHITE-PRIMARY",
  WHITE_SECONDARY = "WHITE-SECONDARY",
  DANGER = "DANGER",
}

export enum ButtonType {
  BUTTON = "button",
  SUBMIT = "submit",
  RESET = "reset",
}

export interface ButtonProps {
  label: string;
  type?: ButtonType;
  variant?: Variant;
  disabled?: boolean;
  iconLeft?: string;
  iconRight?: string;
  href?: string;
  external?: boolean;
  onClick?: MouseEventHandler<HTMLButtonElement>;
}
