.button--primary, .button {
  padding: 0.5rem 1.5rem;
  margin-right: 0.5rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  background: #7549ff;
  border: 2px solid #7549ff;
  border-radius: 0.2rem;
  color: hsl(0, 0%, 100%);
  font-family: "Satoshi Variable", sans-serif;
  font-weight: 500 !important;
  text-decoration: none !important;
  text-align: center;
  flex-shrink: 0;
  cursor: pointer;
}
.button--primary--white, .button--white {
  background: hsl(0, 0%, 100%);
  border-color: hsl(0, 0%, 100%);
  color: #7f63ef;
}
.button--primary--white:hover, .button--white:hover {
  background: rgba(255, 255, 255, 0.8);
  border-color: hsl(0, 0%, 100%);
  color: #9385ff;
}
@media (prefers-reduced-motion: no-preference) {
  .button--primary, .button {
    transition: background 0.2s ease-in-out;
  }
}
.button--primary:hover, .button:hover {
  background: #7f5bff;
  border-color: #7f5bff;
  color: hsl(0, 0%, 100%);
}
.button--primary[disabled], .button[disabled] {
  background: hsl(251, 16.9%, 12.7%) !important;
  border-color: hsl(251, 16.9%, 12.7%) !important;
  color: hsl(252, 11.3%, 45.1%) !important;
  cursor: not-allowed !important;
}
.button--primary[disabled]:before, .button[disabled]:before {
  content: "‚ö†¬†";
}

.button--secondary {
  background: transparent;
  border: 2px solid #7549ff;
  color: hsl(0, 0%, 100%);
}
.button--secondary:hover {
  background: #7549ff;
  color: hsl(0, 0%, 100%);
}
.button--secondary--white {
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.8);
  color: rgba(255, 255, 255, 0.8);
}
.button--secondary--white:hover {
  background: rgba(255, 255, 255, 0.2);
  color: hsl(0, 0%, 100%);
  border-color: hsl(0, 0%, 100%);
}

.button--danger {
  background: transparent;
  border: 2px solid #ff427b;
  color: hsl(0, 0%, 100%);
}
.button--danger:hover {
  background: #ff5c8d;
  border-color: #ff5c8d;
  color: hsl(0, 0%, 100%);
